import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {ListModel} from "../../models/list";
import {ListItemModel} from "../../models/listitem";
import {RestapiServiceProvider} from "../../providers/restapi-service/restapi-service";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  search: string;
  path: string;

  public hmodels: ListModel[] = [];
  public listmodel: { [id: string] : ListModel } = {};
  public categories: { [id: string] : ListItemModel} = {};
  public translations: { [id: string] : string} = {};

  constructor(public navCtrl: NavController, public restapiService: RestapiServiceProvider) {
    this.getCategory("root");
    this.getCategoryList("root");

    this.hmodels.push(this.listmodel['root']);
  }

  onlistclick(event) {

  }

  popModel() {
    this.search = "";
    this.hmodels.pop();
  }

  getTranslation(id) {
    let path = "translation/" + id;

    this.restapiService.getData(path)
      .then(data => {
        let _data: any = data;
        console.log("hello")
        console.log(JSON.stringify(_data));
        this.translations[id] = _data.names["fr-FR"];
      });
  }

  getCategoryList(id) {
    if (id in this.categories) {
      for (let subid of this.categories[id].sublist) {
        this.getCategory(subid, this.listmodel[id]);
      }
    }
  }

  getCategory(id, listmodel=null) {
    let path = "category/" + id;

    this.restapiService.getData(path)
      .then(data => {
        let _data: any = data;
        this.categories[id] = new ListItemModel(_data.name, _data.image, _data.subcat);
        if (listmodel != null && listmodel.items.find(this.categories[id]) == null)
          listmodel.push(this.categories[id]);

        this.getTranslation(_data.name);
      });
  }
}



